<template>
  <div>
    <!-- 头部导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <div>
      <!-- 搜索框 -->
      <el-col :span="24">
        <el-card shadow="always">
          <el-input
            v-model="userQuery.query"
            clearable
            @clear="userList"
            placeholder="请输入内容"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="userList"></el-button>
          </el-input>
          <el-button type="primary" @click="showModel=true">添加用户</el-button>
        </el-card>
      </el-col>

      <!-- 用户列表 表格-->
      <el-table :data="userItem" border style="width: 100%" stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label="姓名"></el-table-column>
        <el-table-column prop="email" label="邮箱"></el-table-column>
        <el-table-column prop="mobile" label="电话"></el-table-column>
        <el-table-column prop="role_name" label="角色"></el-table-column>
        <el-table-column prop="mg_state" label="状态">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="changeStatus(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column prop label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" icon="el-icon-edit" @click="editMessage(scope.row.id)"></el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete" @click="deleteuser(scope.row.id)"></el-button>
            <el-tooltip
              class="item"
              effect="dark"
              content="分配角色"
              placement="top"
              :enterable="false"
            >
              <el-button type="warning" size="mini" icon="el-icon-setting"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 添加用户对话框 -->
      <el-dialog title="添加用户" :visible.sync="showModel" width="40%" @close="closeDialog">
        <!-- 表单验证 -->
        <el-form :model="addItem" :rules="addRules" ref="addRefs" label-width="70px">
          <el-form-item label="用户名" prop="username">
            <el-input v-model="addItem.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="addItem.password"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="addItem.email"></el-input>
          </el-form-item>
          <el-form-item label="手机" prop="mobile">
            <el-input v-model="addItem.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showModel = false">取 消</el-button>
          <el-button type="primary" @click="onSure">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 修改用户对话框 -->
      <el-dialog title="提示" :visible.sync="editDialog" width="40%" @close="editClose">
           <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
          <el-form-item label="用户名" prop="username">
            <el-input v-model="editForm.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="editForm.email" ></el-input>
          </el-form-item>
          <el-form-item label="手机" prop="mobile">
            <el-input v-model="editForm.mobile" ></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialog = false">取 消</el-button>
          <el-button type="primary" @click="editSure">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="userQuery.pagenum"
        :page-sizes="[1,2,3,5]"
        :page-size="userQuery.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    var checkEmail = (rule, value, cb) => {
      const regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        return cb()
      }
      cb(new Error('请输入合法的邮箱'))
    }
    var CheckMobile = (rule, value, cb) => {
      const regMobile = /^[1][3,4,5,7,8][0-9]{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error('请输入有效手机号'))
    }
    return {
      userQuery: {
        query: '',
        pagenum: 1,
        pagesize: 1,
      },
      userItem: [],
      total: 0,
      showModel: false,
      addItem: {
        username: '',
        password: '',
        email: '',
        mobile: '',
      },
      addRules: {
        username: [
          { required: true, message: '请输入', trigger: 'blur' },
          { min: 2, max: 6, message: '长度在2 到 6 个字符', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            min: 6,
            max: 16,
            message: '长度在 6 到 16 个字符',
            trigger: 'blur',
          },
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' },
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: CheckMobile, trigger: 'blur' },
        ],
      },
      editDialog:false,
      editForm:{
        // username:'',
        // email:'',
        // mobile:''
      },
      editFormRules:{
          email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' },
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: CheckMobile, trigger: 'blur' },
        ],
      }
    }
  },
  methods: {
    async userList() {
      const { data: res } = await this.$http.get('users', {
        params: this.userQuery,
      })
      window.console.log(res)
      if (res.meta.status != 200) return this.$message.error('获取用户列表失败')
      this.userItem = res.data.users
      this.total = res.data.total
      // window.console.log(this.total)
    },
    handleSizeChange(newPage) {
      // window.console.log(newPage)
      this.userQuery.pagesize = newPage
      this.userList()
    },
    handleCurrentChange(newPage) {
      // window.console.log(newPage)
      this.userQuery.pagenum = newPage
      this.userList()
    },
    // 修改用户状态
    async changeStatus(userInfo) {
      window.console.log(userInfo)
      const { data: res } = await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      )
      window.console.log(res)
      if (res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('修改用户状态失败')
      }
      this.$message.success('修改用户状态成功')
    },
    closeDialog() {
      this.$refs.addRefs.resetFields()
    },
    onSure() {
      this.$refs.addRefs.validate(async (valid) => {
        if (!valid) return
        // 可以添加用户的网络请求
        const { data: res } = await this.$http.post('users', this.addItem)
        window.console.log(res)
        if (res.meta.status != 201) return this.$message.error('添加用户失败')
        this.$message.success('添加用户成功')
        this.showModel = false
        this.userList()
      })
    },
  async editMessage(id){
      window.console.log(id)
     const {data:res}=await this.$http.get(`users/${id}`)
     window.console.log(res)
     if(res.meta.status!=200) return this.$message.error('用户信息查询失败')
     this.$message.success('用户信息查询成功')
     this.editForm=res.data
      this.editDialog=true
    },
    editClose(){
      this.$refs.editFormRef.resetFields()
    },
    editSure(){
      this.$refs.editFormRef.validate(async valid=>{
        if(!valid) return
        const {data:res}=await this.$http.put('users/'+this.editForm.id,{email:this.editForm.email,mobile:this.editForm.mobile})
        window.console.log(res)
        if(res.meta.status!=200) return this.$message.error('编辑失败')
        this.$message.success(res.meta.msg)
        this.userList()
      })
      this.editDialog=false
    },
   async deleteuser(id){
     console.log(id)
        const confirmResult=await this.$confirm(
          '此操作将永久删除还用户，是否继续？',
          '提示',
          {
            confirmButtonText:'确定',
            cancelButtonText:'取消',
            type:'warning'
          }
        ).then(err=>err)
        if(confirmResult!=='confirm'){
          return this.$message.info('已取消删除')
        }
        
        const {data:res}=await this.$http.delete(`users/${id}`)
        // console.log(res)
        if(res.meta.status!=200) return this.$message.error('删除失败')
         this.$message.success('删除成功')
         this.userList()
    }

  },
  mounted() {
    this.userList()
  },
}
</script>

<style>
.el-col {
  margin-top: 10px;
}
.input-with-select {
  width: 30%;
}
.el-button {
  margin-left: 15px;
}
.el-pagination {
  margin-top: 15px;
}
</style>